import {
    number_format
} from '../../components/my-components/formater.js'
import {
    setData
}from '../../store/cache.js'
export default {
    props: {
        info: {
            type: Object,
            default: {
                title: '',
                subTitle: "",
                today: 0,
                yesterday: 0
            }
        }
    },
    methods: {
        clickModel() {
            
            this.Cacher.setData('dataGraph',this.info);
            uni.navigateTo({
                url: './moneyDetail?from=dataGraph'
            })
        },
        formater(num) {
            return number_format(num)
        }
    },
}
